import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import axios from 'axios';
import Parser from 'html-react-parser';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      valueN: 0,
      displayTable:false,
      words:''
    };
  }
  changeHandler(event) {
    this.setState({valueN: event.target.value});
  }
  submitHandler(event) {
   let data = {
      count: this.state.valueN
    }
    // console.log(JSON.stringify(data))
    axios.post('https://assignmentttt.herokuapp.com/words', JSON.stringify(data),{

      headers: {
        'Content-Type':'application/json',
      }

    }).then((response) => {
        console.log(response)
        this.setState({displayTable:false})
        if(response.data.length>0)
        {
          this.setState({words:'<table className="wordsTable"><thead><tr><th>Word </th><th>Frequency</th></tr></thead><tbody>'})
          response.data.map((data) => {
            let temp = this.state.words + '<tr><td>'+ data.wordName + '</td><td>'+ data.count + '</td></tr>'
            this.setState ({
              words : temp
            })
        })
          let temp = this.state.words + '</tbody></table>'
          this.setState({
            words : temp,
            displayTable: true
        })  
        }
        
      })
  }
  render() {
    return (
      <div className="App">
        
        <p className="form">
          Enter the Value of N
        </p>
        <input type="number" value={this.state.valueN} onChange={this.changeHandler.bind(this)} />
        <button onClick={this.submitHandler.bind(this)}>Submit</button>
        {this.state.displayTable ? Parser(this.state.words) : null}
        

      </div>
    );
  }
}

export default App;



